html {
    height: 100%;
  }
  body {
    min-height: 100%;
    line-height: 1;
    font-family: sans-serif;
  }
  
  #frontContainer{
    min-width: 100%;
    min-height: 100%;
    width:100vh;
    height: 100vh;
  }
  
  #frontImage {
      min-width: 100%;
      min-height: 100%;
      background: url(../images/hal.jpg) center center no-repeat;
      width:100vh;
      height: 100vh;
     
  }
#frontSpacingContainer {
    display: flex;
    flex-direction: row;
    min-width: 100%;
    min-height: 100%;
}
  
#frontRowOne {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: top;
    min-width: 25vw;
}
  
#frontLogin {
    height: 200px;
    width: 200px;
    background-color:black;
    display: flex;
    flex-direction: column;
    align-items: center;
}
  
#frontNameContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80px;
    width: 175px;
    color: white;
}
  
#frontPwContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80px;
    width: 175px;
    color: white;
}
  
#frontRowTwo {
    min-width: 50vw;
}
  
#frontRowThree {
    min-width: 25vw;
}

#buttons {
      display: flex;
      flex-direction: row;
      align-items: center;
}

.App {
    text-align: center;
}
  
.App-logo {
    height: 40vmin;
    pointer-events: none;
}
  
@media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
}
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  